import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Main.vue'

Vue.use(VueRouter)

const routes = [{
    path: '*',
    redirect: '/'
}, {
    path: '/',
    name: 'Home',
    component: Home,
}]

const router = new VueRouter({
    routes
})

export default router