          contentInSide:
            "基本操做 docker pull、rm等，透過 Docker 建立本地端與容器間的映射連結 Server，並透過 Node Js 介接由 Docker 建立的 Sql Server 虛擬機 Server。",
        },
      ],
    };
  },
  mounted() {
    document
      .querySelectorAll(".imgs")
      .forEach((key, index) => key.setAttribute("data-num", (index += 1)));
    document
      .querySelectorAll(".nav-option")
      .forEach((key, index) => key.setAttribute("data-num", (index += 1)));
  },
  methods: {
    scrollContentPart() {
      let rwdEqual = this.rwdStatus == true ? 4 : 2;
      const downArrow = document.querySelector(".down-arrow");
      const mainSkillOuter = document.querySelector(".main-skill-outer");
      const collectionOuter = document.querySelector(".collection-outer");
      const hobbyOuter = document.querySelector(".hobby-outer");
      const selfOuter = document.querySelector(".self-outer");
      const screenScrollY = window.scrollY + window.innerHeight;
      const selfPos = selfOuter.offsetTop;
      const arrowPos = downArrow.offsetTop + downArrow.offsetHeight / rwdEqual;
      const hobbyPos =
        hobbyOuter.offsetTop + hobbyOuter.offsetHeight / rwdEqual;
      const skillPos =
        mainSkillOuter.offsetTop + mainSkillOuter.offsetHeight / rwdEqual;
      const collectionPos =
        collectionOuter.offsetTop + collectionOuter.offsetHeight / 2 / rwdEqual;
      this.scrollContent(
        screenScrollY,
        arrowPos,
        skillPos,
        collectionPos,
        hobbyPos,
        selfPos,
        selfOuter
      );
    },
    scrollItems(selfOuter) {
      if (window.scrollY >= 55) {
        if (window.scrollY < this.scrollTemp) {
          this.rwdTitleAnimate = false;
          this.rwdIconOptionsAnimate = false;
        } else {
          this.byHand == false
            ? (this.rwdTitleAnimate = true)
            : setTimeout(() => (this.rwdTitleAnimate = true), 3050);
          this.rwdIconAnimate == true
            ? (this.rwdIconOptionsAnimate = false)
            : (this.rwdIconOptionsAnimate = true);
        }
        this.scrollTemp = window.scrollY;
      }
      if (window.scrollY >= 150) {
        this.goTopAnimate = true;
        selfOuter.style.paddingTop = "";
      } else {
        this.goTopAnimate = false;
        this.rwdStatus == false
          ? (selfOuter.style.paddingTop = "150px")
          : (selfOuter.style.paddingTop = "65px");
      }
    },
    scrollContent(
      screenScrollY,
      arrowPos,
      skillPos,
      collectionPos,
      hobbyPos,
      selfPos,
      selfOuter
    ) {
      this.scrollItems(selfOuter);
      arrowPos < screenScrollY
        ? (this.arrowAnimate = true)
        : (this.arrowAnimate = false);

      if (selfPos < screenScrollY) {
        this.showNavBg(0);
        this.selfAnimate = true;
        setTimeout(() => (this.selfDetailsAnimate = true), 1001);
      } else {
        this.selfAnimate = false;
        this.selfDetailsAnimate = false;
      }

      if (hobbyPos < screenScrollY) {
        this.showNavBg(1);
        this.selfOuterAnimate = true;
        setTimeout(() => (this.hobbyAnimate = true), 700);
      } else {
        this.selfOuterAnimate = false;
        this.hobbyAnimate = false;
      }

      if (skillPos < screenScrollY) {
        this.showNavBg(2);
        this.hobbyOuterAnimate = true;
        setTimeout(() => (this.mainSkillAnimate = true), 700);
      } else {
        this.mainSkillAnimate = false;
        this.hobbyOuterAnimate = false;
      }
      if (collectionPos < screenScrollY) {
        this.showNavBg(3);
        this.mainSkillOuterAnimate = true;
        setTimeout(() => (this.collectionAnimate = true), 700);
      } else {
        this.collectionAnimate = false;
        this.mainSkillOuterAnimate = false;
      }
    },
    showNavBg(num) {
      if (this.rwdStatus == true) return;
      let navBody = document
        .querySelector(".nav-option")
        .getBoundingClientRect();
      setTimeout(
        () => document.querySelector(".nav-bg").classList.add("nav-bg-active"),
        100
      );
      document.querySelector(".nav-bg").style.left = `${
        num == 0 ? navBody.left : navBody.width * num
      }px`;
      document.querySelector(".nav-bg").style.width = `${navBody.width}px`;
      document.querySelector(".nav-bg").style.height = `${navBody.height}px`;
    },
    hideNavBg() {
      document.querySelector(".nav-bg").classList.remove("nav-bg-active");
    },
    goTop() {
      $("html, body").animate({ scrollTop: 0 }, 2500);
    },
    goToTitle(title) {
      this.byHand = true;
      setTimeout(() => (this.byHand = false), 3050);
      this.goToTitleName.forEach((key) => {
        if (key.titleNum == title) {
          $("html, body").animate(
            { scrollTop: $(`${key.titleName}`)[0].offsetTop },
            3000
          );
        }
      });
      if (this.rwdStatus == true) {
        this.showRwdMenu();
        return;
      }
    },
    showRwdMenu() {
      if (this.rwdIconAnimate == false) {
        this.rwdIconAnimate = true;
        document.querySelector(".nav-rwd-outer").style.display = "block";
        setTimeout(() => (this.rwdMenuAnimate = true), 100);
      } else {
        this.rwdMenuAnimate = false;
        this.rwdIconAnimate = false;
        this.rwdTitleAnimate == false
          ? (this.rwdIconOptionsAnimate = false)
          : (this.rwdIconOptionsAnimate = true);
        setTimeout(
          () => (document.querySelector(".nav-rwd-outer").style.display = ""),
          901
        );
      }
    },
    showSkill(num) {
      let pos = this.skillNumTemp.findIndex((key) => key == num);
      if (pos == -1) {
        this.skillNumTemp.push(num);
        [...document.querySelectorAll(".skill-card-body")][num].classList.add(
          "skill-card-body-show"
        );
        [...document.querySelectorAll(".skill-card-body-outer")][
          num
        ].classList.add("skill-card-body-outer-toggle");
      } else {
        this.skillNumTemp.forEach((key) => {
          if (key == num) {
            [...document.querySelectorAll(".skill-card-body-outer")][
              key
            ].classList.remove("skill-card-body-outer-toggle"),
              [...document.querySelectorAll(".skill-card-body")][
                key
              ].classList.remove("skill-card-body-show");
          }
        });
        this.skillNumTemp.splice(pos, 1);
      }
    },
    showCollectContent(num) {
      let pos = this.collectNumTemp.findIndex((key) => key == num);
      if (pos == -1) {
        this.collectNumTemp.push(num);
        [...document.querySelectorAll(".collect-content")][num].classList.add(
          "collect-content-show"
        );
        [...document.querySelectorAll(".arrows")][
          num
        ].childNodes[0].classList.add("line-ft-animate"),
          [...document.querySelectorAll(".arrows")][
            num
          ].childNodes[1].classList.add("line-sd-animate");
      } else {
        this.collectNumTemp.forEach((key) => {
          if (key == num) {
            [...document.querySelectorAll(".collect-content")][
              key
            ].classList.remove("collect-content-show");
            [...document.querySelectorAll(".arrows")][
              num
            ].childNodes[0].classList.remove("line-ft-animate"),
              [...document.querySelectorAll(".arrows")][
                num
              ].childNodes[1].classList.remove("line-sd-animate");
          }
        });
        this.collectNumTemp.splice(pos, 1);
      }
    },
  },
  created() {
    this.$router.push("/descript", () => {});
    window.addEventListener("scroll", this.scrollContentPart);
    window.innerWidth <= 1024
      ? (this.rwdStatus = true)
      : (this.rwdStatus = false);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollContentPart);
  },
};
</script>